<template>
  <v-card height="auto">
    <v-card-text>
      <v-row>
        <v-col cols="12">
          <p class="text-h4 white--text">Wallet</p>
        </v-col>
        <v-col cols="12" class="d-flex">
          <p class="text-h5 white--text">Transport:</p>
          <p class="text-h5 white--text px-md-6">{{ Transport }}</p>
        </v-col>
        <v-col cols="12" class="d-flex">
          <p class="text-h5 white--text">Wallet Address:</p>
          <p class="text-h5 white--text px-md-6">{{ Address }}</p>
        </v-col>
        <v-col class="text-h5 d-md-flex d-block" cols="12">
          <p class="white--text flex-grow-0 flex-shrink-0">Setting Address:</p>
          <v-select
            :items="items"
            v-model="trans"
            class="px-md-4 flex-grow-1 flex-shrink-1"
            dense
            dark
            label="Transport"
            outlined
          ></v-select>
          <v-text-field
            v-model="wallet"
            class="px-md-4 flex-grow-1 flex-shrink-0"
            dark
            outlined
            dense
            clearable
            label="Please enter the address"
          ></v-text-field>
          <v-btn
            class="flex-grow-0 flex-shrink-0"
            color="primary"
            depressed
            height="40"
            @click="walletSend()"
            >Send</v-btn
          >
        </v-col>
        <v-col cols="12" class="text-end">
          <v-btn
            class="ml-6"
            color="primary"
            depressed
            height="40"
            :to="{ name: 'Deposit' }"
            >Deposit</v-btn
          >
        </v-col>
      </v-row>
    </v-card-text>

    <div class="text-center">
      <v-dialog class="div-field" v-model="dialog" hide-overlay persistent>
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Please enter the address
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
  </v-card>
</template>

<script>
import { changeWallet } from "../../api";

export default {
  data: () => ({
    wallet: "",
    dialog: false,
    items: ["TRC20", "ERC20"],
    trans: "TRC20",
    walletAddress: [],
  }),
  created() {
    this.$store.dispatch("getUserData");
  },
  computed: {
    Transport() {
      return this.Wallet[0];
    },

    Address() {
      return this.Wallet[1];
    },

    Wallet() {
      return this.$store.state.wallet.split(",");
    },
  },
  methods: {
    //錢包地址送出
    walletSend() {
      return new Promise((resolve, reject) => {
        if (this.wallet) {
          this.walletAddress.push(this.trans);
          this.walletAddress.push(this.wallet);
          console.log(this.walletAddress);

          if (this.walletAddress[1]) {
            resolve(this.sendWalletApi());
          } else {
            reject();
          }
        } else {
          this.reject();
        }
      });
    },

    //如果沒有寫地址就拒絕
    reject() {
      this.dialog = true;
      setTimeout(() => {
        this.dialog = false;
      }, 2000);
      this.walletAddress = [];
    },

    //錢包地址綁定api
    sendWalletApi() {
      var params = new URLSearchParams();
      params.append("Wallet", this.walletAddress);

      changeWallet(params)
        .then((result) => {
          console.log(result);
          if (result.data.success == true) {
            alert("Wallet address has been bound");
            location.reload();
          } else {
            alert(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //儲值金額輸入後進到確認購買訂單
    toCheckOrder() {
      if (this.money == 0 || this.money == "") {
        alert("請先輸入金額");
      } else {
        this.dialog2 = true;
      }
    },

    //確認訂單後執行匯款api
    checkedOrder() {
      const api = `https://www.jinhaoye.cc/api/users/remit_money`;
      const vm = this;
      var params = new URLSearchParams();
      params.append("Money", vm.money);

      vm.$http
        .post(api, params)
        .then((result) => {
          if (result.data.success == true) {
            alert("儲值訂單已送出");
            location.reload();
          } else {
            alert(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};
</script>